    getColorBtnVoBo (vobo) {
      if (vobo === 'N') {
        return 'red'
      } else if (vobo === 'S') {
        return 'primary'
      } else {
        return 'gray'
      }
    },
    getColorMinuta (minutaOK) {
      if (minutaOK > 0) {
        return 'primary'
      } else {
        return 'red'
      }
    },
    formatoValorIn () {
      this.valorproceso = this.$limpiarMoneda(this.valorproceso)
    },
    formatoValorOut () {
      this.valorproceso = this.$formatonumero(this.valorproceso, 2, 1)
    },
    async propuestasAdjudicadas () {
      try {
        const { data } = await adminApp.propuestasAdjudicadas()
        this.dataConsulta = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async oferentePropuestaAdjudicada (codigopropuesta, nombre, opendialog = true) {
      try {
        const { data } = await adminApp.oferentePropuestaAdjudicada({ codigopropuesta: codigopropuesta })
        this.oferentes = data
        this.tituloOferentes = nombre
        this.identificacion = data[0].identificacion
        this.nombrecontratista = data[0].Nombre
        if (opendialog) {
          this.dialog_oferentes = true
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async buscarResponsableJuridico () {
      this.readvbjuridica = true
      try {
        const useractual = this.$session.get('usuarioid')
        const { data } = await adminApp.userXArea({ area: 7 })
        if (data.data) {
          if (parseFloat(data.data[0].userid) === parseFloat(useractual)) {
            this.readvbjuridica = false
          }
        }
      } catch (error) {
      } finally {
      }
    },
    async traeAbogados () {
      try {
        const { data } = await adminApp.personalPorArea({ area: 7 })
        if (data.data) {
          this.arrAbogados = data.data.filter(persona => persona.codigoestado === 1)
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async agregarVoBo () {
      if (this.vobojefjur === '' || this.voboobservacion === '') {
        this.$toasted.error('Debe ingresar los datos', { icon: 'error', duration: 5000 })
        return
      }
      const { data } = await adminApp.guardarVoBoPropuestaConvocatoria({
        proceso: this.itemvobo.id,
        vobojefjur: this.vobojefjur,
        voboobservacion: this.voboobservacion
      })
      if (data.result) {
        if (data.result === 'ok') {
          this.$toasted.success(data.mensaje, { icon: 'check', duration: 5000 })
          this.limpiarDatos()
          this.propuestasAdjudicadas()
          this.dialog_vobo = false
        } else {
          this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Se presentó un error al intentar agregar o actualizar la información', { icon: 'error', duration: 5000 })
      }
    },
    limpiarDatos () {
      this.abogadoproceso = ''
      this.supervisorproceso = ''
      this.valorproceso = 0
      this.fechacontrato = ''
      this.numerocontrato = ''
      this.nombrecontratosecop = ''
      this.valortotalcontrato = 0
      this.fechasuscripcioncontrato = ''
      this.numeropolizas = 0
      this.fechaaprobacionpoliza = ''
      this.fechaterminacioninicial = ''
      this.fechaterminaciondefinitiva = ''
      this.observaciones = ''
      this.fechainicio = ''
    },
    abrirDialogoDatos (item) {
      this.oferentePropuestaAdjudicada(item.id, item.titulo, false)
      this.item = item
      this.limpiarDatos()
      this.abogadoproceso = item.abogado
      this.supervisorproceso = item.supervisor
      this.valorproceso = this.$formatonumero(item.valor, 2, 1)
      this.fechacontrato = item.fecha_contrato
      this.numerocontrato = item.numero_contrato
      this.nombrecontratosecop = item.nombre_contrato_secop
      this.valortotalcontrato = this.$formatonumero(item.valor_total_contrato, 2, 1)
      this.fechasuscripcioncontrato = item.fecha_suscripcion_contrato
      this.numeropolizas = item.numero_polizas
      this.fechaaprobacionpoliza = item.fecha_aprobacion_poliza
      this.fechaterminacioninicial = item.fecha_terminacion_inicial
      this.fechaterminaciondefinitiva = item.fecha_terminacion_definitiva
      this.observaciones = item.observaciones
      this.fechainicio = item.fecha_inicio
      this.dialog_datosadicionales = true
    },
    abrirDialogoVoBo (item) {
      this.vobojefjur = item.vobojefjur
      this.voboobservacion = item.voboobservacion
      this.itemvobo = item
      this.dialog_vobo = true
    },
    abrirDialogoDocumentos (item, tipo) {
      switch (tipo) {
        case 'RC':
          this.titulodialogo = 'Documentación requerida para la contratación'
          break
        case 'TP':
          this.titulodialogo = 'Documentación para trámite de póliza'
          break
        case 'LC':
          this.titulodialogo = 'Documentación legalización contractual'
          break
        default:
          break
      }
      this.idregistro = item.registro
      this.tipodoc = tipo
      this.idpropconv = item.id
      this.dialog_docrequerida = true
    },
    abrirMinuta (item) {
      this.idpropconv = item.id
      this.dialog_minuta = true
    },
    abrirDialogoNovedad (item) {
      this.oferentePropuestaAdjudicada(item.id, item.titulo, false)
      this.itemNovedad = item
      this.dialog_novedades = true
    },
    abrirDialogoFinalizaproceso (item) {
      this.item = item
      this.dialog_finalizaproceso = true
    },
    async finalizarProceso () {
      const { data } = await adminApp.finalizarProceso({ idpropconv: this.item.id })
      if (data) {
        this.$toasted.success('Proceso finalizado con éxito', { icon: 'check', duration: 5000 })
        this.dialog_finalizaproceso = false
      }
      this.propuestasAdjudicadas()
    }
  },
  mounted () {
    this.propuestasAdjudicadas()
    this.traeAbogados()
    this.buscarResponsableJuridico()
  }
}
</script>
